import "./skills.css"
import { useContext} from "react"
import { ThemeContext } from "../../context"

const Skills = () => {
    
    const theme = useContext(ThemeContext);
    const darkMode = theme.state.darkMode;

    return (
        <div className="skills" id="skills">
            <div className="skills-text">
                <h1 className="skills-title">Skills</h1>
            </div>
            <div className="skills-list">
                <ul>
                    <li style={{color: darkMode && "#b6b8ba"}}>JavaScript</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>ReactJS</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>JQuery</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>Ajax</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>PHP</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>Laravel</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>Yii</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>MySQL</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>Bootstrap</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>HTML5</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>CSS3</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>REST</li>
                    <li style={{color: darkMode && "#b6b8ba"}}>JSON</li>
                </ul>
            </div>
        </div>
    )
}

export default Skills
