import "./navBar.css"
import * as Icon from 'react-bootstrap-icons';
import { useState } from "react"
// import { ThemeContext } from "./context";
// import { useContext } from "react";

    const NavBar = () => {

    const [clicked, setClicked] = useState(false)
    // const theme = useContext(ThemeContext);
    // const darkMode = theme.state.darkMode;
    
    const handleClick = (e) => {
        setClicked(!clicked)
    }

    return (
    <nav className ="header" >
        <div id="mobile-menu" className="shadow-large" onClick={handleClick}>
            {clicked? <Icon.XCircle className="icon-menu"/> : <Icon.List className="icon-menu"/> }
        </div>
        {/* style={{display: clicked ? 'block': 'none'}} */}
        <ul className="menu shadow" style={{display: clicked ? 'block': ''}}>
            <li>
                <a href="#about" className="navLinks">About</a>
            </li>
            <li>
                <a href="#projects" className="navLinks">Projects</a>
            </li>
            <li style={{display:'none'}}>
                <a href="#experience" className="navLinks">Experience</a>
            </li>
            <li>
                <a href="#skills" className="navLinks">Skills</a>
            </li>
            <li>
                <a href="#contact" className="navLinks">Contact</a>
            </li>
        </ul>
    </nav>
    )
}

export default NavBar
