import React from 'react'
import "./about.css"
import AboutImage from '../../img/about-img.jpg'
const About = () => {
    return (
        <div className="about" id="about">
            <div className="about-left">
                <div className="about-card bg"></div>
                <div className="about-card">
                    <img src={AboutImage} alt="" className="about-image" />
                </div>
            </div>
            <div className="about-right">
                <h1 className="about-title">About Me</h1>
                <p className="about-sub-t">Demonstrated history of working in information technology and logistic firm, developed web applications using Laravel and ReactJS.</p>
                <p className="about-sub-t">Committed to maintaining cutting edge technical skills and dedicated on building performance API, high functional Web Applications, and Websites. </p>
            </div>
        </div>
    )
}

export default About
